body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    background-color: black;
    
}

a {
    color: white;
    font-weight: bold;
    font-family: sans-serif;
    text-decoration: none;
}

.container {
    position: relative;
}

.navigation {
    z-index: 2;
    text-align: center;
    padding: 80px 0;
    position: relative;
    box-shadow: 10px 0 200px rgba(0, 0, 0, 0.9), /* Umbra din st√¢nga */
                -10px 0 200px rgba(0, 0, 0, 0.9); /* Umbra din dreapta */
}

.logo {
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translate(-50%, -50%);
}

.logo img {
    width: 200px;
}

.menu {
    display: flex;
    justify-content: center;
    margin-top: 56px;
}

.left-menu,
.right-menu {
    display: flex;
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.left-menu {
    transform: translate(-25%, 0%);
    margin-right: 180px;
}

.right-menu {
    transform: translate(-25%, 0%);
    margin-left: 180px;
}

.left-menu li,
.right-menu li {
    line-height: 1.5;
    margin: 0 53px;
}

.backround-image {
    position: relative;
    opacity: 0.30 ; 
    width: 100%;
    
}

.backround-slogan {
    color: wheat;
    position: absolute;
    top: 100%;
    left: 10%;
    transform: translate(-50%, -50%);
    font-family: sans-serif;
    font-size: 16px;
}

.backround-slogan1 {
    color: rgb(165, 161, 161);
    position: absolute;
    top: 106%;
    left: 10%;
    transform: translate(-50%, -50%);
    font-family: sans-serif;
    font-size: 24px;
}

.street {
    position: absolute;
    top: 100%;
    left: 88%;
    transform: translate(-50%, -50%);
    font-family: sans-serif;
    font-size: 24px;
    color: rgb(165, 161, 161);
}

.phone {
    color: rgb(165, 161, 161);
    position: absolute;
    top: 110%;
    left: 88%;
    transform: translate(-50%, -50%);
    font-family: sans-serif;
    font-size: 24px;
}

/* icon Scisoors */
.scissors-item {
    fill: rgb(82, 82, 10);
    cursor: pointer;
    
}

.scissors-list {
    position: relative;
    top: -90px;
    left: -5px;
 
}


/* animatie de deschidere */
.scissors-item:hover  {
    transform: rotate(45deg);
    transition: transform 0.5s ease-in-out;
    
} 

 .scissors-item:hover  {
    transform: rotate(45eg);
    transition: transform 0.5s ease-in-out;
} 
/* animatie de inchidere */
  .scissors-item  { 
    transform: rotate(0deg);
    transition: transform 0.9s ease-in-out;
}

.scissors-item  {
    transform: rotate(0deg);
    transition: transform 0.5s ease-in-out;
}
.left-scissors:hover {
    transform: rotate(45eg);
    transition: transform 0.5s ease-in-out;
}
/* final icon scissors */

